body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --blue: #007bff;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.logo img {
  height: 40px;
  display: block;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-list li {
  margin: 0 20px;
}

.nav-list li:first-child {
  margin-left: 0;
}

.nav-list li:last-child {
  margin-right: 0;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav-link:hover {
  color: var(--blue);
}

.is-active {
  color: var(--blue);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  width: 100%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
}
